import { Component, ChangeDetectorRef, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController, Loading, PopoverController, Platform } from 'ionic-angular';
import { ToolsProvider } from '../../providers/tools/tools';
import { DomSanitizer } from '@angular/platform-browser';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { ActivityServiceProvider } from '../../providers/activity-service/activity-service';
import { CommonServiceProvider } from '../../providers/common-service/common-service';
import { ServiceForm } from '../../model/service-form-model';
import { DatePipe } from '@angular/common';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { NotificationProvider } from '../../providers/notification/notification';

/**
 * Generated class for the ActivitiesBookingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-activities-booking',
  templateUrl: 'activities-booking.html',
})
export class ActivitiesBookingPage {
  @ViewChild('dateSelector') dateSelector: ElementRef;
  loading: Loading;
  activityBookingForm: FormGroup; // registration form
  backgroundImage: any; // to store background image url
  descriptionHtml: any; // to store 'description' translation  
  res: any; //to store return data while language translate
  item: any = []; // to store item 
  personArray: any = []; // to store person for select option 
  suffix: any; // suffix to select option of person
  turnList: any = []; //turn list
  currencySymbol: any = this.hotelProvider.objHotel.currency.signo; // currency symbol
  dateType: string; // date type
  timeSlots: any = []; // time slots
  monthNames: any; // for month names
  monthShortNames: any = []; // for month short names
  minDate: any = new Date();
  maxDate: any;
  dayShortNames: any = [];
  firstSuffix: any;
  platFormIsIOS: boolean = false;
  hideBackButton: boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, public toolsPvdr: ToolsProvider, public sanitized: DomSanitizer, public cdRef: ChangeDetectorRef, public formBuilder: FormBuilder, public alertCtrl: AlertController, public reservationProvider: ReservationServiceProvider, public activityProvider: ActivityServiceProvider, public commonProvider: CommonServiceProvider, public popoverCtrl: PopoverController, private datePipe: DatePipe, private hotelProvider: HotelServiceProvider, private notificationProvider: NotificationProvider, private loadingCtrl: LoadingController, private platForm: Platform) {

    this.item = this.navParams.get('item'); // gets item 

    this.activityBookingForm = formBuilder.group({
      'date': new FormControl(null, [Validators.required]),
      'turn': new FormControl(null, [Validators.required]),
      'time': new FormControl(null),
      'noOfPersons': new FormControl('', [Validators.required]),
      'specialRequests': new FormControl('')
    });

    // translate month names
    let monthList = this.toolsPvdr.fnLanguageTranslate("common.monthNames");
    monthList.subscribe((value: string) => {
      this.monthNames = value;
    });
    //translated month short names
    for (let i = 0; i < 12; i++) {
      this.monthShortNames.push(this.toolsPvdr.getShortMonthName(i));
    }
    //translated day short names
    for (let j = 0; j < 7; j++) {
      this.dayShortNames.push(this.toolsPvdr.getShortDayName(j));
    }
    if (this.platForm.is("ios")) {
      this.platFormIsIOS = true;
    }
  }

  ionViewDidLoad() {
    console.log(this.commonProvider.objFormData);
    this.hideBackButton = false;
    this.backgroundImage = this.item.imgPath; // returns image url
    let minPersons = this.item.modoReserva.minPersonas ? this.item.modoReserva.minPersonas : 0; // to set minimum value to select option of person
    let maxPersons = this.item.modoReserva.maxPersonas ? this.item.modoReserva.maxPersonas : 10; // to set maximum value to select option of person
    this.activityBookingForm.get("noOfPersons").setValue(this.item.modoReserva.minPersonas);// set minimum number of person as default value
    this.res = this.toolsPvdr.fnLanguageTranslate(['view.reservation.label.legal', 'view.reservation.label.people.suffix', 'view.reservation.label.people.suffixFirst']); // language translation
    this.res.subscribe((value: string) => {
      this.descriptionHtml = this.sanitized.bypassSecurityTrustHtml(value['view.reservation.label.legal']);
      this.suffix = value['view.reservation.label.people.suffix'];
      this.firstSuffix = value['view.reservation.label.people.suffixFirst'];
      this.cdRef.detectChanges();
      this.maxDate = this.toolsPvdr.fnDateFormatChange(this.commonProvider.calculateMaxDate());
      this.minDate = this.toolsPvdr.fnGetToday();
      this.dateType = this.item.modoReserva.tipo;
      if (this.item.modoReserva.tipo == "T") {
        this.turnList = this.item.modoReserva.turnos;
      }
      if (this.item.modoReserva.tipo == "C") {
        this.timeSlots = this.commonProvider.fnConvertToTimeSlotsArray(this.item.modoReserva.franjas[0].start, this.item.modoReserva.franjas[0].end, this.item.modoReserva.franjas[0].increment);
        //to set start date based on time slots available
        let lastTimeInterval = this.timeSlots.length > 0 ? this.timeSlots[this.timeSlots.length - 1] : "0:00";
        this.minDate = this.toolsPvdr.fnSetStartDate(lastTimeInterval);
      }
    });

    let minText = minPersons + ' ' + this.suffix;
    if (minPersons == 1) {
      minText = minPersons + ' ' + this.firstSuffix;
    }
    this.personArray.push({ text: minText, value: minPersons, checked: true }); //set values in personArray
    for (var i = minPersons + 1; i <= maxPersons; i++) {
      this.personArray.push({ text: i + ' ' + this.suffix, value: i, checked: false });
    }
    this.fnCheckPreEnteredFormData();
  }

  ionViewWillEnter() {
    this.hideBackButton = false;
  }
  // store entered data into model before leaving page
  public ionViewWillLeave() {
    if (this.commonProvider.objFormData.activity.bookingCompleted != true) {
      this.commonProvider.objFormData.activity.id = this.item.id;
      this.commonProvider.objFormData.activity.date = this.activityBookingForm.get('date').value;
      this.commonProvider.objFormData.activity.time = this.activityBookingForm.controls['time'] && this.activityBookingForm.get('time').value;
      this.commonProvider.objFormData.activity.turn = this.activityBookingForm.controls['turn'] && this.activityBookingForm.get('turn').value;
      this.commonProvider.objFormData.activity.noOfPersons = this.activityBookingForm.get('noOfPersons').value;
      this.commonProvider.objFormData.activity.comments = this.activityBookingForm.get('specialRequests').value;
      this.commonProvider.objFormData.activity.bookingCompleted = false;
    }
    this.hideBackButton = true;
  }
  // fetch pre-entered data into fields from model
  fnCheckPreEnteredFormData() {
    if (this.commonProvider.objFormData && this.commonProvider.objFormData.activity) {
      this.commonProvider.objFormData.activity.bookingCompleted = false;
      if (this.commonProvider.objFormData.activity.id == this.item.id) {
        this.activityBookingForm.get("date").setValue(this.commonProvider.objFormData.activity.date);
        this.activityBookingForm.controls['time'] && this.activityBookingForm.get("time").setValue(this.commonProvider.objFormData.activity.time);
        this.activityBookingForm.controls['turn'] && this.activityBookingForm.get("turn").setValue(this.commonProvider.objFormData.activity.turn);
        this.activityBookingForm.get("noOfPersons").setValue(this.commonProvider.objFormData.activity.noOfPersons);
        this.activityBookingForm.get("specialRequests").setValue(this.commonProvider.objFormData.activity.comments);
      }
    }
  }

  fnSubmit(item) {
    if (this.activityBookingForm.controls['date'].hasError('required')) {
      let validateMessages = this.toolsPvdr.fnLanguageTranslate(['view.reservation.error.nullDate', "common.error", "buttons.ok"]);

      validateMessages.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: value['view.reservation.error.nullDate'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);
      });
    } else if (this.reservationProvider.objReservation.alreadyVerifiedCheckOut) {

      let cantBookMsg = this.toolsPvdr.fnLanguageTranslate(['view.reservation.error.checkoutYaRealizado', "common.error", "buttons.ok"]);

      cantBookMsg.subscribe((value: string) => {
        let alertbook = this.alertCtrl.create({
          title: value['common.error'],
          message: value['view.reservation.error.checkoutYaRealizado'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertbook.present();
        setTimeout(() => { alertbook.dismiss(); }, 5000);
      });

    } else {
      let confirmMessages = this.toolsPvdr.fnLanguageTranslate(['buttons.send', 'buttons.modify', 'view.reservation.confirm', 'common.confirm']); //for language translation

      confirmMessages.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.confirm'],
          message: value['view.reservation.confirm'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.modify'],
              role: 'cancel',
              handler: data => {

              }
            },
            {
              text: value['buttons.send'],
              handler: data => {
                //date format changes for nostay user
                let bookingDate: any;
                let time = this.activityBookingForm.controls['time'] && this.activityBookingForm.get('time').value ? this.activityBookingForm.get('time').value + ':00' : '00:00:00';

                bookingDate = this.toolsPvdr.dateFormatChangeForService(this.activityBookingForm.controls['date'] && new Date(this.activityBookingForm.get('date').value)).split(" ")[0] + ' ' + time;

                if ((item.paymentRequiredNoStay && this.reservationProvider.objReservation.noStayGuest) || (item.paymentRequired && !this.reservationProvider.objReservation.noStayGuest)) { // if payment required is true then redirecting to payment form

                  //commonProvider
                  this.commonProvider.objServiceForm = new ServiceForm();
                  this.commonProvider.objServiceForm.serviceId = item.id;
                  this.commonProvider.objServiceForm.date = bookingDate;
                  this.commonProvider.objServiceForm.turn = this.activityBookingForm.controls['turn'] && this.activityBookingForm.get('turn').value ? this.activityBookingForm.get('turn').value : '';
                  this.commonProvider.objServiceForm.noOfPersons = this.activityBookingForm.get('noOfPersons').value;
                  this.commonProvider.objServiceForm.comments = this.activityBookingForm.get('specialRequests').value;
    
                  this.navCtrl.push("ActivityPaymentPage", { item: item }, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
                } else {
                  // if payment required is false 
                  this.showLoading();
                  let serviceId = item.id;
                  let date = bookingDate;
                  let turn = this.activityBookingForm.controls['turn'] && this.activityBookingForm.get('turn').value ? this.activityBookingForm.get('turn').value : '',
                    person = this.activityBookingForm.get('noOfPersons').value,
                    comments = this.activityBookingForm.get('specialRequests').value,
                    reservationId = this.reservationProvider.objReservation.reservationId;

                  this.activityProvider.sendActivityReservation(serviceId, date, turn, person, comments, reservationId).subscribe(data => {
                    this.loading.dismiss();
                    if (data.success == true) {
                      this.activityBookingForm.reset();
                      this.commonProvider.objFormData.activity = [];
                      this.commonProvider.objFormData.activity.bookingCompleted = true;
                      // success message translate
                      let successMessages = this.toolsPvdr.fnLanguageTranslate(['view.reservation.success', "common.requestReceived", "buttons.ok"]);

                      successMessages.subscribe((value: string) => {
                        let alertsuccess = this.alertCtrl.create({
                          title: value['common.requestReceived'],
                          message: value['view.reservation.success'],
                          cssClass: 'cls_alrt',
                          buttons: [
                            {
                              text: value['buttons.ok'],
                              role: 'cancel',
                              handler: data => {
                                this.navCtrl.popTo(this.navCtrl.getByIndex(1), { animate: true, animation: 'transition', duration: 500, direction: 'back' });
                              }
                            }]
                        });
                        alertsuccess.present();
                        setTimeout(() => {
                          alertsuccess.dismiss();
                          this.navCtrl.popTo(this.navCtrl.getByIndex(1), { animate: true, animation: 'transition', duration: 500, direction: 'back' });
                        }, 5000);
                      });
                    }
                  });
                }

              }
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);
      });

    }
  }

  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  public fnDateChange() {

    let date = this.toolsPvdr.fnDateFormatChange(this.activityBookingForm.controls['date'] && new Date(this.activityBookingForm.get('date').value));
    let isToday: boolean;
    let today = this.toolsPvdr.fnGetToday();
    if (date == today) {
      isToday = true;
    } else {
      isToday = false;
    }

    //inserts all time intervals into array timeSlots when multiple time intervals are possible
    if (this.item.modoReserva.tipo == "C" && (this.item.modoReserva.franjas.length > 0)) {
      this.timeSlots = [];
      for (let i = 0; i < this.item.modoReserva.franjas.length; i++) {
        let tmpTimeArray = this.commonProvider.fnConvertToTimeSlotsArray(this.item.modoReserva.franjas[i].start, this.item.modoReserva.franjas[i].end, this.item.modoReserva.franjas[i].increment, isToday);
        tmpTimeArray = tmpTimeArray.indexOf("0:00") > -1 ? tmpTimeArray.filter(e => e != "0:00") : tmpTimeArray;    //filters out "0:00"
        this.timeSlots = this.timeSlots.concat(tmpTimeArray);
      }
    }
  }
  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }
}





